.Todolist{
  display: flex;
  border: 1px solid #BDECB6;
  width: 80%;
  min-height: 55px;
  padding: 25px;
  justify-content: space-between;
  align-items: center;
  font-size: 35px;
  margin: 20px 0;
  border-radius: 15px;
  background: #89AC76;

  .Todolist-edit{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 80%;
    input{
      width: 40%;
      height: auto;
      padding: 15px;
      outline: none;
      font-size: 30px;
      border-radius: 10px;
    }
    button{
      margin-left: 15px;
      outline: none;
      border: none;
      height: auto;
      font-size: 40px;
      padding: 10px;
      border-radius: 10px;
      &:hover{
        background: #BDECB6;
        transform: scale(1.1);
      }
    }
  }

  .Todolist-control{
    cursor: pointer;

    .control-btn{
      margin: 0 10px;
      font-size: 40px;

      &:hover{
        transform: scale(1.3);
      }
    }

  }
}

