.TodoForm{
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
  .TodoForm__input{
    width: 600px;
    min-height: 50px;
    font-size: 35px;
    outline: none;
    padding: 20px;
    border-radius: 10px;
  }
  .TodoForm__btn{
    width: 300px;
    min-height: 50px;
    font-size: 35px;
    border-radius: 10px;
    border: none;
    background: #BDECB6;
    margin-left: 20px;
    &:hover{
      background: cadetblue;
    }
  }
}